.main {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.main .name {
  background-color: #63bb40;
  font-size: 25px;
  width: 40%;
  display: flex;
  align-self: flex-start;
  justify-content: center;
  padding: 10px;
  margin-bottom: 10px;
}
.main .sex {
  align-self: flex-end;
  background-color: #e64f4f;
}
.main .calories .name {
  background-color: #63bb40;
  font-size: 25px;
  width: 40%;
  display: flex;
  align-self: flex-start;
  justify-content: center;
  padding: 10px;
  margin-bottom: 10px;
}
.main .calories .ppm {
  background-color: #363636;
  color: white;
}
.main .calories .sex {
  align-self: flex-end;
  background-color: #e64f4f;
}
.main .navbar {
  width: 95%;
  height: 20%;
  background-color: #037703;
  color: white;
  display: flex;
  padding-right: 5%;
  justify-content: flex-end;
  align-items: center;
  font-size: 20px;
}
.main form {
  height: 60%;
  justify-content: center;
  align-self: center;
  display: flex;
  flex-direction: column;
  width: 50%;
}
